import { Observable, of, from } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ILookupHttpService, RemoteParams, LookupGridResult } from '@farris/ui-lookup';
import { SysCommonUtilService, SysDataUtil } from '@gsp-sys/sysmgr-common';
import { map } from 'rxjs/operators';
import { userHelp, posHelp, roleHelp, userGroupHelp, posGroupHelp, roleGroupHelp, sysOrgHelp } from '../help-meta/sys-help-meta';
import { TreeNode } from '@farris/ui-treetable';
import { SysLocaleService } from '../../locale/sys-locale.service';
import { TreeFilter } from '../filter/tree-filter';


/**
 * å¸®å©æå¡
 */
@Injectable()
export class SysFarrisLookupService implements ILookupHttpService {

    private filterData: any;
    private i18nRes: any;
    constructor (
        private http: HttpClient,
        private sysUtil: SysCommonUtilService,
        private localService: SysLocaleService
    ) {
    }

    /** åå§è¿æ»¤æ¡ä»¶ */
    private setFilterData (filter: any) {
        // æè¿æ»¤æ¡ä»¶
        if (filter) {
            const str = JSON.stringify(filter);
            if (str !== '{}') {
                // ä¸å®è¦å¤å¶
                this.filterData = JSON.parse(str);
            } else {
                this.filterData = null;
            }
        } else {
            this.filterData = null;
        }
    }
    /**
     * æ·»å æ°æ®
     */
    private setListData (src: any[], data: any[]) {
        if (src) {
            src.push.apply(src, data);
        }
    }
    /** è·åè¿æ»¤æ°æ® */
    private getFilterData (): any {
        return this.filterData;
    }
    /**
     * å¼æ­¥è·åçæ¬
     */
    getTreeDataAsync(url: string, params?: RemoteParams): Observable<LookupGridResult> {
        this.i18nRes = this.localService.getComponentOpt('help');
        // è§£ææ¥è¯¢jsonå¯¹è±¡
        const treeParam = JSON.parse(params.searchValue);
        let farrisSelectedData = params['customData'].farrisSelectedData;

        let filter: TreeFilter = {};
        // filter.withPermission = true;
        const category = treeParam.category;
        // 1.é¦æ¬¡å è½½
        if (category === 'all') {
            filter.layer = 1;
        }
        // 2.å¼æ­¥å±å¼ä¸çº§
        if (category === 'children') {
            if (treeParam.hasOwnProperty('parentPath')) {
                filter.path = treeParam.parentPath;
            }
            if (treeParam.hasOwnProperty('parentLayer')) {
                // åç«¯æç´¢ä»¥ä¸çº§èç¹ä¸ºçº§æ°çèç¹
                filter.layer = treeParam.parentLayer + 1;
            }
            if (treeParam.hasOwnProperty('parentId')) {
                filter.parentId = treeParam.parentId;
            }
        }
        // 3.æç´¢
        if (category === 'search') {
            const searchField = treeParam.searchField;
            const searchValue: string = treeParam.searchValue.trim();
            if (searchValue) {
                filter.isSearchTree = true;
                switch (searchField) {
                    case 'code':
                        filter.code = searchValue;
                        break;
                    case 'name':
                        filter.name = searchValue;
                        break;
                    // å¨é¨åæç´¢ï¼ä¹åæ¯'*'
                    case '*':
                    default:
                        filter.codeOrName = searchValue;
                        break;
                }
            } else {
                // æç´¢ä¸ºç©ºï¼éæ°å è½½ï¼é»è®¤ä»1çº§å¼å§
                filter.layer = 1;
            }
        }

        //å¦ææ¯ç»ç»ï¼è¯·æ±éä¸­æ°æ®æ¶
        if (url === '/api/runtime/sys/v1.0/sysOrgs' && category === 'fav') {

            let newUrl = url+'/nullorg' 
            const newOpt = {
                headers: this.sysUtil.getHeader()['headers'],
                params: { useView : 'true'}
            }
            return this.http.get(newUrl, newOpt).pipe(map((val: any[]) => {
                const gridResult: LookupGridResult = {};
                return gridResult;
            }));
        }

        // è¯´ææè¿æ»¤æ¡ä»¶
        if (this.getFilterData()) {
            filter = Object.assign(this.getFilterData(), filter);
            SysDataUtil.deleteEmpty(filter);
        }
        const paramStr = JSON.stringify(filter);
        const opt = {
            headers: this.sysUtil.getHeader()['headers'],
            params: { param: paramStr }
        };
        return this.http.get(url, opt).pipe(map((val: any[]) => {
            const gridResult: any = {};
            gridResult.selectedData = farrisSelectedData;

            gridResult.items = [];
            gridResult.treeInfo = {
                loadDataType: 'async',
                layerType: 'parentId',
                dataField: 'treeInfo',
                pathField: 'path',
                layerField: 'layer',
                parentField: 'parentId',
                isDetailField: 'isDetail'
            };
            gridResult.treeInfo['treeDataIsInit'] = true;
            gridResult.columns = [
                { field: 'code', title: this.i18nRes.codeField },
                { field: 'name', title: this.i18nRes.nameField }];
            gridResult.searchFields = [
                { label: this.i18nRes.codeField, value: 'code' },
                { label: this.i18nRes.nameField, value: 'name' }
            ];
            // å¿é¡»è¦èµå¼
            from(val).subscribe(item => {
                if (item.layer === 1 || !item.parentId) {
                    item.parentId = null;
                }
                item.treeInfo = {
                    parentId: item.parentId,
                    path: item.path,
                    layer: item.layer,
                    isDetail: item.isDetail
                };
            });
            // å¦ææ¯æç´¢çè¯ï¼éè¦èªå·±æé æ 
            if (filter.isSearchTree) {
                gridResult.items = this.makeTreeByParent(val, 'parentId', 'id');
                console.log(gridResult.items);
            } else {
                // æç»æï¼æ®éåå±å è½½
                if (val && val.length > 0) {
                    from(val).subscribe(item => {
                        gridResult.items.push(this.convertToTreeNode(item));
                    });
                }
            }
            return gridResult;
        }));
    }

    /**
     * å°æ èç¹è½¬æ¢ä¸ºFarris TreeNode
     */
    private convertToTreeNode (src: any): TreeNode {
        const node: TreeNode = {};
        node.data = src;
        node.children = [];
        node.selectable = src.farris_selectable;
        node.leaf = src.isDetail;
        node.parent = src.parentId;
        // è¿ä¸æ¡å¿é¡»å¾å ï¼å¦åä¸è¡
        node.id = node.data.id;
        // æç´¢çæ¶åææ«çº§æ·»å äºè¿ä¸ªå±æ§ï¼ä¸éè¦å±å¼
        if (src.hasOwnProperty('expanded')) {
            node.expanded = src.expanded;
        }
        return node;
    }

    /** æç§parentç æé æ  */
    makeTreeByParent (datas: any[], parentField: string, keyField: string): TreeNode[] {
        // ä¸ç´ç²ç©ºï¼æèæ²æä¸ç´ç
        const pathLayerOnes = datas.filter(x => {
            if (!x[parentField]) {
                return true;
            }
            // æ­å±çè¿ç§ï¼åºè¯¥ä¹å±äºä¸çº§
            const p = datas.find(parent => parent[keyField] === x[parentField]);
            if (!p) {
                return true;
            }
        });
        const treedata: TreeNode[] = [];
        pathLayerOnes.forEach(x => {
            const node = this.convertToTreeNode(x);
            treedata.push(node);
        });
        treedata.forEach(item => {
            this.findChildrenByParent(item, datas, parentField, keyField);
        });
        return treedata;
    }

    /** æ¾å°å­èç¹ */
    findChildrenByParent (parent: TreeNode, datas: any[], parentField: string, keyField: string) {
        const childList = datas.filter(childitem => {
            if (childitem[parentField] === parent.data[keyField]) {
                return true;
            }
        });
        if (childList && childList.length > 0) {
            childList.forEach(child => {
                const node = this.convertToTreeNode(child);
                parent.children.push(node);
                this.findChildrenByParent(node, datas, parentField, keyField);
            });
        }
    }

    /**
     * å¨é¨è·åçæ¬çæ¬
     */
    getTreeData (url: string, params?: RemoteParams): Observable<LookupGridResult> {
        let filter: any = {};
        // è¯´ææè¿æ»¤æ¡ä»¶
        if (this.getFilterData()) {
            filter = Object.assign(this.getFilterData(), filter);
            SysDataUtil.deleteEmpty(filter);
        }
        const paramStr = JSON.stringify(filter);
        const opt = {
            headers: this.sysUtil.getHeader()['headers'],
            params: { param: paramStr }
        };
        return this.http.get(url, opt).pipe(map((val: any[]) => {
            if (val && val.length > 0) {
                val.forEach(item => {
                    if (item.layer == 1 || item.parentId == null) {
                        item.parentId = '';
                    }
                    // æ»¡è¶³åç«¯ç»æ
                    item.treeInfo = {
                        parentId: item.parentId,
                        path: item.path,
                        layer: item.layer,
                        isDetail: item.isDetail
                    };
                    item.selectable = false;
                });
            } else {
                val = [];
            }
            return { items: val };
        }));
    }

    /** è·ååè¡¨æ°æ® */
    getGridData (url: string, params?: RemoteParams): Observable<LookupGridResult> {
        let index = params.pageIndex;
        let size = params.pageSize;
        let farrisSelectedData = params['customData'].farrisSelectedData;

        // ç®åè¿ä¸ªå°æ¹æé®é¢ï¼ç¬¬ä¸æ¬¡çæ¶åä¼ éä¸è¿æ¥
        if (!index) {
            const customData = params['customData'];
            index = customData.filter.pageIndex;
            size = customData.filter.pageSize;
        }
        let param: any = {
            pageIndex: index,
            pageSize: size,
        };
        // æ¯å¦ææç´¢
        const filterStr = params.searchValue;
        const filterObj = JSON.parse(filterStr);
        const searchField = filterObj.searchField;
        const searchValue = filterObj.searchValue;
        // æè¿æ»¤æ¥è¯¢,æå¯è½æç´¢å¼ä¸ºç©ºï¼è¿æ¶åæ¯ä¸éè¦æç´¢ç
        if (searchField && searchValue) {
            switch (searchField) {
                case 'code':
                    param.code = searchValue;
                    break;
                case 'name':
                    param.name = searchValue;
                    break;
                // å¨é¨åæç´¢ï¼ä¹åæ¯'*'
                case '*':
                default:
                    param.codeOrName = searchValue;
                    break;
            }
        }
        // è¯´ææè¿æ»¤æ¡ä»¶
        if (this.getFilterData()) {
            param = Object.assign(this.getFilterData(), param);
            SysDataUtil.deleteEmpty(param);
        }
        console.log(param);
        const paramStr = JSON.stringify(param);
        const opt = {
            headers: this.sysUtil.getHeader()['headers'],
            params: { param: paramStr }
        };
        return this.http.get(url, opt).pipe(map((val: any) => {
            if (val && val.data) {
                // å°æ¬æ¬¡åé¡µçæ°æ®éå å°å½åå¸®å©çåè¡¨ä¸­
                this.setListData(params['customData'].allData, val.data);
            }
            return {
                items: val.data,
                total: val.totalCount,
                pageInfo: {
                    pageIndex: index,
                    pageSize: size,
                    pageList: [20, 40, 60, 80, 100],
                    enablePager: true
                },
                selectedData:farrisSelectedData
            };
        }));
    }
    /** åæ° */
    getData (url: string, params?: RemoteParams): Observable<LookupGridResult> {
        console.log(params);
        console.log(url);
        const filter = params['customData'].filter;
        this.setFilterData(filter);
        const typeIndex = url.indexOf('\/');
        const httpUrl = url.substring(typeIndex, url.length);
        const helpType = url.substring(0, typeIndex);
        switch (helpType) {
            case userHelp:
            case posHelp:
            case roleHelp:
                return this.getGridData(httpUrl, params);
            case userGroupHelp:
            case posGroupHelp:
            case roleGroupHelp:
            case sysOrgHelp:
                return this.getTreeDataAsync(httpUrl, params);
            default:
                break;
        }
    }
}
