/**
 * ç³»ç»ç®¡çï¼åè½æä½å·¦æ æãå³åè¡¨å±ç¤º
 */
@Component({
    selector: 'app-sys-farris-func-operation',
    templateUrl: './sys-farris-func-operation.component.html',
    styleUrls: ['./sys-farris-func-operation.component.css']
})
export class SysFarrisFuncOperationComponent implements OnInit,AfterContentInit {

    /** ä¸å¡å¯¹è±¡,è´è´£ä¼ é */
    @Input() bizObjectId: string;
    /** éæ©æ¨¡å¼ */
    @Input()
    gridSelectMode: 'single' | 'multiple';
    multiSelect = false;
    /** å·²ç»åºå¼ */
    @Input()
    sessionId: string;
    /** æ¯å¦åªæ¾ç¤ºåç» */
    @Input()
    onlyShowGroup = false;
    /** åç»è½å¦è¢«éæ© */
    @Input()
    isGroupCanSelected: boolean;
    /**
     * ç¡®è®¤äºä»¶
     */
    @Output()
    afterConfirm = new EventEmitter<any[]>();
    /**
     * å³é­äºä»¶
     */
    @Output()
    afterCancle = new EventEmitter<any>();
    // æ³¨å¥å°å¸®å©çç¡®è®¤ååæ¶æé®
    @ViewChild('btnOkAndCancle')
    btnOkAndCancleRef: TemplateRef<any>;
    /**
     * åè½æä½å¨éæ°æ®
     */
    gridDataSrc: any[];
    treeDataSrc: any[];
    /**
     * å½åUIæ¾ç¤ºå¨è½æä½åè¡¨æ°æ®
     */
    gridData: any[];
    treeData: any[];
    /**
     * å½åéä¸­æ°æ®
     */
    curSelected:any[]=[];
    curSelectedKeys: string[] = [];
    gridColumns: any[] = [];
    treeColumns: any[];
    opSearchTxt = null;
    i18nRes: any;
    @ViewChild('operationGrid') operationGrid: DatagridComponent;
    @ViewChild('farrisTreeParent') farrisTreeParent: ElementRef;
    @ViewChild('tt') farrisTt: TreeTableComponent;
    /**
     * çæ§ææ½äºä»¶ï¼éç»ç»ç»TreeTable
     * @param event äºä»¶åæ°
     */
     @HostListener('window:resize', [])
     winResize() {
         this.reSizeTreeTable();
     }
    constructor(
        private funcOpService: SysFuncOperationService,
        private bizObjectService: BizObjectService,
        private localService: SysLocaleService
    ) {
    }
    /** éç»Farris-TreeTableçå¤§å° */
    public reSizeTreeTable (height: number = 0, width: number = 0) {
        if (height === 0) {
            height = this.farrisTreeParent.nativeElement.clientHeight;
        }
        if (width === 0) {
            width = this.farrisTreeParent.nativeElement.clientWidth;
        }
        this.farrisTt.resize({ height, width });
    }
    ngOnInit() {
        this.operationGrid.restService = null;
        this.operationGrid.virtualized = true;
        this.operationGrid.fitColumns = true;
        this.operationGrid.fit = true;
        if (this.localService) {
            this.i18nRes = this.localService.getComponentOpt('help');
        } else {
            this.i18nRes = HELP_LOCALE_ZHCHS;
        }
        // åªæ¾ç¤ºåç»ï¼åè½èåä½¿ç¨
        if (this.onlyShowGroup) {
            this.gridColumns = [
                { field: 'code', title: this.i18nRes.codeField, width: 120 },
                { field: 'name', title: this.i18nRes.nameField, width: 150 }
            ];
            // ç§ææ¹æ³ï¼æ´æ°å
            // this.operationGrid['columnsChanged']();
        } else {
            this.gridColumns = [
                { field: 'code', title: this.i18nRes.codeField, width: 120 },
                { field: 'name', title: this.i18nRes.nameField, width: 150 },
                { field: 'parentId', title: this.i18nRes.opGroupField, formatter: this.funcGroupFormatter }
            ];
        }
        this.treeColumns = [
            { field: 'name', title: this.i18nRes.busTitle, width: 100 }
        ];
        // å¤é
        if (this.gridSelectMode === 'multiple') {
            this.multiSelect = true;
        }
        this.loadData();
    }

    /** é¡µé¢æ¸²æå®æå */
    ngAfterContentInit (): void {
        this.reSizeTreeTable();
    }
    
    /**
     * å è½½æ°æ®,TODO å è½½æ°æ®
     */
    loadData () {
        // å è½½ä¸å¡å¯¹è±¡æ°æ®
        this.bizObjectService.getBizObjects().subscribe((result: any) => {
            this.treeDataSrc = result;
            this.treeData = this.makeTree(result);
        });
        // å è½½åè½æä½æ°æ®
        this.funcOpService.getFuncOperations().subscribe(lists => {
            // åªæ¾ç¤ºåç»
            if (this.onlyShowGroup) {
                this.gridDataSrc = lists.filter(item => item.type == 0);
            } else {
                this.gridDataSrc = lists;
            }
            // ä¸å®è¦å»¶è¿ä¸æ®µæ¶é´
            this.freshGridAndSelect(this.gridDataSrc.concat());
        },
            error => {
                console.log('è·ååè½æä½å¯¹è±¡åè¡¨å¤±è´¥');
            }
        );
    }

    /**
     * å·æ°åè¡¨æ¾ç¤º
     */
    private freshGridAndSelect (list: any[]) {
        this.operationGrid.clearAll();
        this.operationGrid.loadData(list);
        list.forEach(item => {
            if(this.curSelectedKeys.indexOf(item.id)>-1){
                this.operationGrid.checkRow(item.id,false);
                if(this.curSelected.indexOf(item)===-1){
                    this.curSelected.push(item);

                }
            }
        });
    }

    /**
     * æé æ 
     */
    private makeTree (data: any[]): TreeNode[] {
        const root = data.filter(t => t.layer === 1).map(t => {
            return {
                data: t,
                children: [],
                expanded: false,
                selectable: true
            };
        });
        root.forEach(e => {
            // æé ä¸çº§ï¼childrenï¼ç¶çº§çidä¿¡æ¯
            e.children = this.makeChildrenByParentId(data, e.data.id);
        });
        // é»è®¤å±å¼ç¬¬ä¸ä¸ªèç¹
        root[0].expanded = true;
        return root;
    }

    /**
     * ç¶èç¹æé æ 
     */
    private makeChildrenByParentId (datas: any[], parentId: string): any[] {
        const x = datas.filter(c => c.parentID === parentId).map(d => {
            return {
                data: d,
                children: this.makeChildrenByParentId(datas, d.id),
                expanded: false,
                selectable: true
            };
        });
        return x;
    }
    /**
     * æç´¢æ¶çæµåè½¦
     */
    onKeyUp (arg: any) {
        if (arg && arg.which === 13) {
            this.searchData();
        }
    }
    /**
     * æç´¢
     */
    searchData () {
        // æµè¯
        if (!this.opSearchTxt) {
            // å¨éçæ°æ®
            this.gridData = this.gridDataSrc;
            this.freshGridAndSelect(this.gridData);
            return;
        }
        const content = this.opSearchTxt.trim();
        if (!content) {
            // å¨éçæ°æ®
            this.gridData = this.gridDataSrc;
            this.freshGridAndSelect(this.gridData);
            return;
        }
        const filter = content.toLowerCase();
        // å¦æåªæ¾ç¤ºåç»
        if (this.onlyShowGroup) {
            this.gridData = this.gridDataSrc.filter(item =>
                item.code.toLowerCase().includes(filter) ||
                item.name.toLowerCase().includes(filter));
        } else {
            // éè¦æ¾ç¤ºæä½çæ¶åï¼æç´¢å°ä¸çº§æ¶
            this.gridData = this.gridDataSrc.filter(item =>
                item.code.toLowerCase().includes(filter) ||
                item.name.toLowerCase().includes(filter) ||
                this.getFuncGroup(item).toLowerCase().includes(filter));
        }
        this.freshGridAndSelect(this.gridData);
    }

    /**
     * æ ¼å¼å
     */
    funcGroupFormatter = (val, data) => {
        return this.getFuncGroup(data);
    }

    /**
     * è·ååè½åç»
     */
    getFuncGroup (funcOpObj: any): string {
        // è·åç¶èç¹
        const parentId = funcOpObj.parentId;
        if (parentId) {
            const parentObj = this.gridDataSrc.filter(item => item.id === parentId);
            if (parentObj !== null && parentObj.length > 0) {
                return parentObj[0].name;
            }
        } else {
            return '';
        }
    }

    /**
     * ä¸å¡å¯¹è±¡ååå
     * @param arg ä¸å¡å¯¹è±¡åæ°
     */
    bizObjectChanged (arg: any) {
        if (!this.gridDataSrc || this.gridDataSrc.length === 0) {
            return;
        }
        const busId = arg.node.data.id;
        // æ ¹æ®ä¸å¡å¯¹è±¡è¿æ»¤
        const lists = this.gridDataSrc.filter(item =>
            item.businessId === busId);
        this.freshGridAndSelect(lists);
    }
    /**
     * è·åéä¸­æ°æ®
     */
    getSelectedObject(): any[] {
        const rows = this.operationGrid.checkeds.map(item => item.data);
        return rows;
    }
    /**
     * éä¸­å
     */
    checked(row) {
        console.log(row);
        if (row && row.id) {
            // æ¯å¦å¤é
            if (this.multiSelect) {
                if(this.curSelectedKeys.indexOf(row.id)<0){
                    this.curSelected.push(row.data);
                    this.curSelectedKeys.push(row.id);
                }

            } else {
                this.curSelected=[row.data];
                this.curSelectedKeys = [row.id];
            }
        }
    }
    /**
     * åæ¶éä¸­åè¡¨
     */
    unchecked(row) {
        console.log(row);
        if (row && row.id) {
            if (this.multiSelect) {
                const x = this.curSelectedKeys.findIndex(item => item === row.id);
                if (x >= 0) {
                    this.curSelectedKeys.splice(x, 1);
                    this.curSelected.splice(x, 1)
                }
            } else {
                this.curSelectedKeys = [];
                this.curSelected = [];

            }
        }
    }
    /**
     * ç¡®è®¤
     */
    confirm () {
        // const result = this.getSelectedObject();
        const result = this.curSelected;
        console.log(result);
        this.afterConfirm.next(result);
        // åæ¶-ç®çæ¯å¨è¿éè®©æ¨¡ææ¡æ¶å¤±
        this.cancle();
    }
    /**
     * åæ¶
     */
    cancle () {
        this.afterCancle.next();
    }
}

