import { forEach } from '@angular/router/src/utils/collection';
import { SysHelpService } from './../service/sys-help.service';
import { Component, OnInit, Input, EventEmitter, Output, ViewChild } from '@angular/core';
import { TreeFilter } from '../filter/tree-filter';
import { LookupGridComponent, ServerSideToken, PickedResult } from '@farris/ui-lookup';
import { SysCommonUtilService } from '@gsp-sys/sysmgr-common';
import { sysOrgHelp, posGroupHelp, roleGroupHelp, userGroupHelp } from '../help-meta/sys-help-meta';
import { SysFarrisLookupService } from '../service/sys-farris-lookup.service';
import { of, Observable } from 'rxjs';
import { SysLocaleService } from '../../locale/sys-locale.service';
import { LanguageService } from '@ecp-caf/caf-common';
import { HELP_LOCALE_ZHCHS } from '../../locale/locales/zh-cn/help';
import { map } from 'rxjs/operators';

@Component({
    selector: 'sys-farris-tree-help',
    templateUrl: './sys-farris-tree-help.component.html',
    styleUrls: ['./sys-farris-tree-help.component.css'],
    providers: [
        { provide: ServerSideToken, useClass: SysFarrisLookupService }
    ]
})
export class SysFarrisTreeHelpComponent implements OnInit {
    /** æ¯å¦åªè¯» */
    @Input() disabled = false;
    /** æ é¢ */
    @Input() title: string;
    /** æ¯å¦åå±å è½½ç»ç» */
    @Input() async = false;
    /** åæ°æº */
    @Input() sourceType: 'org' | 'usergroup' | 'positiongroup' | 'rolegroup';
    /** æ¯å¦åé */
    @Input() singleSelect = true;
    /** ç»å®å°å¸®å©ä¸çæ°æ®ï¼åå§æ¾ç¤ºå¼ç¨ */
    @Input() displayTxt: string;
    /** å­å¨å¸®å©éæ©çå¯¹è±¡ */
    @Input() bindingData: { id: string, name: string };
    /** è¿æ»¤æ¡ä»¶ */
    @Input() filter: TreeFilter;
    /**
     * æå¼å¸®å©åæ¯å¦æéä¸­æ°æ®
     */
    @Input() enableToSelect=true;
    /** å¯ç¨å³é­åäºä»¶ */
    @Input() useBeforeCloseEvent = false;
    @Input() beforeClose: (selectData: any) => Observable<PickedResult>;
    /** æ¯å¦å¼å§æ°æ®æéè¿æ»¤ */
    @Input() withPermission = false;
    /** å¸®å©éæ©åäºä»¶ */
    @Output() afterConfirm: EventEmitter<any> = new EventEmitter<any>();
    /** æ¸ç©ºåäºä»¶-ææ¶ä¸ç¨ */
    @Output() clear: EventEmitter<any> = new EventEmitter<any>();
    @Input() showAllPathOrg:boolean = false;
    @ViewChild('farristreelookup') public farrisLookUp: LookupGridComponent;
    mapFields = { id: 'id', name: 'name' };

    dataUri: string;
    columns: any;
    treeInfo: any;
    public allData = [];
    public i18nRes: any;
    constructor (
        public sysUtil: SysCommonUtilService,
        public localService: SysLocaleService,
        public sysHelpService: SysHelpService,
    ) {
    }

    ngOnInit () {
        if (this.localService) {
            this.i18nRes = this.localService.getComponentOpt('help');
        } else {
            this.i18nRes = HELP_LOCALE_ZHCHS;
        }
        // é»è®¤ä¸åå±å è½½
        this.treeInfo = {
            loadDataType: 'all',
            layerType: 'parentId',
            dataField: 'treeInfo',
            pathField: 'path',
            layerField: 'layer',
            parentField: 'parentId',
            isDetailField: 'isDetail',
            treeDataIsInit: false
        };
        this.columns = [
            { title: this.i18nRes.codeField, field: 'code', searchField: 'code' },
            { title: this.i18nRes.nameField, field: 'name', searchField: 'name' },
        ];
        // å¼æ­¥å è½½çè¯éè¦å¼å¯è¿ä¸¤ä¸ªå¼å³
        if (this.async) {
            this.treeInfo.loadDataType = 'async';
            this.treeInfo.treeDataIsInit = true;
        }
        // webapiåå§
        switch (this.sourceType) {
            case 'org':
                this.dataUri = sysOrgHelp + this.sysUtil.sysOrgWebApi;
                if (!this.title) {
                    this.title = this.i18nRes.userOrgTitle;
                }
                break;
            case 'usergroup':
                this.dataUri = userGroupHelp + this.sysUtil.userGroupWebApi;
                if (!this.title) {
                    this.title = this.i18nRes.userGroupTitle;
                }
                break;
            case 'positiongroup':
                this.dataUri = posGroupHelp + this.sysUtil.posGroupWebApi;
                if (!this.title) {
                    this.title = this.i18nRes.positionGroupTitle;
                }
                break;
            case 'rolegroup':
            default:
                this.dataUri = roleGroupHelp + this.sysUtil.roleGroupWebApi;
                if (!this.title) {
                    this.title = this.i18nRes.roleGroupTitle;
                }
                break;
        }
        // å¿é¡»è¦æï¼å¦ååå§éä¸­ä¸èµ·ä½ç¨
        this.farrisLookUp.mapFields = { id: 'id', name: 'name' };
    }

    /** å¸®å©åäºä»¶,å¼ºè¡ä¼ éè¿æ»¤æ¡ä»¶ */
    beforeOpen = (arg) => {
        //å¦ææè¿æ»¤æ¡ä»¶ï¼éå å°urlè·¯å¾
        let farrisSelectedData = [];
        let selectedData = [];
        let temp = 0;

        if(this.bindingData!=null&&this.bindingData.id!=null&&this.bindingData.id!=""){
            selectedData = this.bindingData.id.split(',');
            let selectedDataName = this.bindingData.name.split(',');

            selectedData.forEach(item=>{
                farrisSelectedData.push({"id":item,"name":selectedDataName[temp]});
                temp = temp+1;

            });
        }
        return of({
            showDialog: true,
            data: {
                filter: this.filter,
                allData: this.allData,
                farrisSelectedData:farrisSelectedData
            }
        });
    }

    /** å¸®å©å³é­åäºä»¶,ç®åæ è®ºæ¯å¦é½ä¼ç»§ç»­èµ°afterOkæ å°äºä»¶ */
    dictPicked = (arg: any): Observable<PickedResult> => {
        if (this.useBeforeCloseEvent && this.beforeClose) {
            return this.beforeClose(arg).pipe(map(val => {
                // å¦æè½å³é­ï¼è°ç¨åäºä»¶
                if (val.closeDialog) {
                    this.afterConfirm.next(arg);
                }
                return val;
            }));
        } else {
            return of({ closeDialog: true });
        }
    }
    /** å¸®å©æ å°åäºä»¶ */
    afterOk = (helpdata, mapfields) => {
        // å¦æå¯ç¨äºå³é­åæ ¡éªäºä»¶ï¼ä¸èµ°è¿é
        if (this.useBeforeCloseEvent) {
            return;
        }
        if (helpdata) {
            // å¤éçè¯éè¦å¤ç,å¸®å©å·²éå·ä¸ºé´é
            if (!this.singleSelect) {
                const idList: string[] = helpdata.map(item => item.id);
                const nameList: string[] = helpdata.map(item => item.name);
                this.bindingData.id = idList.join();
                this.bindingData.name = nameList.join();
                this.displayTxt = this.bindingData.name;
                this.afterConfirm.next(helpdata);

            } else {
                // å¸®å©æ¾ç¤º
                this.displayTxt = helpdata.name;
                if(this.showAllPathOrg){
                    this.sysHelpService.getParentOrg(helpdata).subscribe(items=>{
                        if(items&&items.length>0){
                            helpdata.allPathOrg=items;
                        }
                        this.afterConfirm.next(helpdata);
                        console.log(helpdata);
                    });
                }else{
                    this.afterConfirm.next(helpdata);

                }
            }
        } else {// ç®åæ¸ç©ºä¼èµ°è¿éï¼èä¸ä¸¤æ¬¡
            // è§¦åäºä»¶
            this.afterConfirm.next(helpdata);
            console.log(helpdata);
        }

    }

    /** æ¸ç©ºå */
    afterClear(arg: any) {
        this.clear.next(arg);
    }

    /** æå¨æ¾ç¤ºå¸®å© */
    public showHelp() {
        this.farrisLookUp.showDialog();
    }
    search($event){
        console.log($event);
    }
}
