import { forEach } from '@angular/router/src/utils/collection';
import { Component, OnInit, Input, Output, EventEmitter, ViewChild, Optional } from '@angular/core';
import { userHelp, posHelp, roleHelp } from '../help-meta/sys-help-meta';
import { SysCommonUtilService } from '@gsp-sys/sysmgr-common';
import { ServerSideToken, LookupGridComponent, PickingResult, PickedResult } from '@farris/ui-lookup';
import { SysFarrisLookupService } from '../service/sys-farris-lookup.service';
import { GspUserFilter } from '../filter/gsp-user-filter';
import { GspPositionFilter } from '../filter/gsp-position-filter';
import { GspRoleFilter } from '../filter/gsp-role-filter';
import { Observable, of } from 'rxjs';
import { SysLocaleService } from '../../locale/sys-locale.service';
import { HELP_LOCALE_ZHCHS } from '../../locale/locales/zh-cn/help';
import { map, filter } from 'rxjs/operators';
import { element } from 'protractor';

@Component({
    selector: 'sys-farris-grid-lookup',
    templateUrl: './sys-farris-lookup.component.html',
    styleUrls: ['./sys-farris-lookup.component.css'],
    providers: [
        { provide: ServerSideToken, useClass: SysFarrisLookupService }
    ]
})
export class SysFarrisLookupComponent implements OnInit {

    /** æ é¢ */
    @Input() title: string;
    /**å®½åº¦ */
    @Input() width = 550;
    /** æ¯å¦æ¾ç¤ºç¨æ·æåºå­æ®µï¼WFä½¿ç¨ */
    @Input() showUserOrderField = false;
    /** æ¾ç¤ºç¨æ·çåä½ä¿¡æ¯ï¼WFä½¿ç¨ */
    @Input() showUserUnitInfo = false;
    /** åæ°æº */
    @Input() sourceType: 'user' | 'position' | 'role';
    /** åªè¯» */
    @Input() disabled = false;
    /**
     * å¿å¡«
     */
    @Input() required=true;
    /**
     * æå¼å¸®å©åæ¯å¦æéä¸­æ°æ®
     */
    @Input() enableToSelect=true;
    /** æ¯å¦åé */
    @Input() singleSelect: boolean;
    /** æ¯å¦åé¡µï¼é»è®¤true */
    @Input() pagination = true;
    /** é¦æ¬¡å è½½ç¬¬å é¡µï¼é»è®¤1 */
    @Input() pageIndex = 1;
    /** æ¯é¡µå è½½æ°ï¼é»è®¤20 */
    @Input() pageSize = 20;
    /** ç»å®å°å¸®å©ä¸çæ°æ®ï¼åå§æ¾ç¤ºå¼ç¨ */
    @Input() displayTxt: string;
    /** å­å¨å¸®å©éæ©çå¯¹è±¡ */
    @Input() bindingData: { id: string, name: string };
    /** è¿æ»¤æ¡ä»¶ */
    @Input() filter: GspUserFilter | GspPositionFilter | GspRoleFilter | any;
    /** æ¯å¦å¼å§æ°æ®æéè¿æ»¤ */
    @Input() withPermission = false;
    /** å¯ç¨å³é­åäºä»¶ */
    @Input() useBeforeCloseEvent = false;
    @Input() beforeClose: (selectData: any) => Observable<PickedResult>;
    @Input() typeMode=0;
    /** å¸®å©éæ©åäºä»¶ */
    @Output() afterConfirm: EventEmitter<any> = new EventEmitter<any>();

    /** å¸®å©éæ©åäºä»¶ï¼ä¼è¿åéä¸­åå é¤çid */
    @Output() afterConfirmData: EventEmitter<any> = new EventEmitter<any>();

    /** æ¸ç©ºåäºä»¶-ææ¶ä¸ç¨ */
    @Output() clear: EventEmitter<any> = new EventEmitter<any>();
    /** å³é­å-ææ¶ä¸ç¨ */
    @Output() loadSuccessEvent: EventEmitter<any> = new EventEmitter<any>();
    @Output() valueChangedEvent: EventEmitter<any> = new EventEmitter<any>();
    @Output() selectDataEvent: EventEmitter<any> = new EventEmitter<any>();
    @Output() dialogClosed: EventEmitter<any> = new EventEmitter<any>();
    dataUri: string;
    columns: any[];
    selectedData :any[]=[];
    farrisSelectedData:any[]=[];
    @ViewChild('farrislookup') public farrisLookUp: LookupGridComponent;
    mapFields = { id: 'id', name: 'name' };
    /** æ¯æ¬¡å³é­åæ°æ® */
    public allData: any[];
    public i18nRes: any;
    constructor(
        public sysUtil: SysCommonUtilService,
        public localService: SysLocaleService
    ) {
        this.selectedData = [];

    }

    ngOnInit() {
        if (this.localService) {
            this.i18nRes = this.localService.getComponentOpt('help');
        } else {
            this.i18nRes = HELP_LOCALE_ZHCHS;
        }
        this.columns = [
            { title: this.i18nRes.codeField, field: 'code', searchField: 'code', width: '160' },
            { title: this.i18nRes.nameField, field: 'name', searchField: 'name', width: '200' },
        ];
        // æ¾ç¤ºç¨æ·çåä½è·¯å¾ä¿¡æ¯ï¼WFä½¿ç¨
        if (this.showUserUnitInfo) {
            this.width = 850;
            this.columns.push({ title: this.i18nRes.orgNamePathField, field: 'orgNamePath' });
            this.width = 850;
            this.columns.push({ title: this.i18nRes.orgName, field: 'sysOrgName' });
        }
        // æ¾ç¤ºæåºå­æ®µï¼WFä½¿ç¨
        if (this.showUserOrderField) {
            this.columns.push({ title: this.i18nRes.orderField, field: 'orderNum' });
        }
        // åå§å
        this.allData = [];
        // webapiåå§
        switch (this.sourceType) {
            case 'position':
                this.dataUri = posHelp + this.sysUtil.positionWebApi;
                if (!this.title) {
                    this.title = this.i18nRes.positionTitle;
                }
                break;
            case 'role':
                this.dataUri = roleHelp + this.sysUtil.roleWebApi;
                if (!this.title) {
                    this.title = this.i18nRes.roleTitle;
                }
                break;
            case 'user':
            default:
                this.dataUri = userHelp + this.sysUtil.userWebApi;
                if (!this.title) {
                    this.title = this.i18nRes.userTitle;
                }
                break;
        }
        // å¿é¡»è¦æï¼å¦ååå§éä¸­ä¸èµ·ä½ç¨
        this.farrisLookUp.mapFields = { id: 'id', name: 'name' };
        // å¸®å©å³é­åäºä»¶
        this.farrisLookUp.dialogClosed.subscribe(val => {
            console.log(val);
            this.dialogClosed.next(val);
        });
    }

    /** å¸®å©åäºä»¶,å¼ºè¡ä¼ éè¿æ»¤æ¡ä»¶ */
    beforeOpen = (arg) => {
        if (!this.filter) {
            // å¯è½è°ç¨æ¹ä¸ä¼ éï¼è¯å«ä¸ºéç©º
            this.filter = {};
        }
        /**
         * WFä½¿ç¨ï¼å¦ææ¾ç¤ºç¨æ·çåä½ä¿¡æ¯ï¼éè¦æ¥è¯¢æ¡ä»¶ä¸­å¢å å¦ä¸æ è¯ï¼
         * æå¡å¨ç«¯è§£æ
         */
        if (this.showUserUnitInfo) {
            this.filter.queryMode = '0002';
        }
        this.filter.pageIndex = this.pageIndex;
        this.filter.pageSize = this.pageSize;
        this.allData.length = 0;
        this.farrisSelectedData = [];
        this.selectedData = [];

        if(this.bindingData!=null&&this.bindingData.id!=null&&this.bindingData.id!=""){
            this.selectedData = this.bindingData.id.split(',');
            this.selectedData.forEach(item=>{
                this.farrisSelectedData.push({"id":item});

            });
        }

        // æè¿æ»¤æ°æ®åå½åçæ°æ®ä¼ éå°serviceå±
        return of({
            showDialog: true, data: {
                filter: this.filter,
                allData: this.allData,
                farrisSelectedData:this.farrisSelectedData
            }
        });
    }

    /** å¸®å©å³é­åäºä»¶,ç®åæ è®ºæ¯å¦é½ä¼ç»§ç»­èµ°afterOkæ å°äºä»¶ */
    dictPicked = (arg: any): Observable<PickedResult> => {
        if (this.useBeforeCloseEvent && this.beforeClose) {
            return this.beforeClose(arg).pipe(map(val => {
                // å¦æè½å³é­ï¼è°ç¨åäºä»¶
                if (val.closeDialog) {
                    this.afterConfirm.next(arg);
                }
                return val;
            }));
        } else {
            return of({ closeDialog: true });
        }
    }

    /** å¸®å©æ å°åäºä»¶ */
    afterOk = (helpdata, mapfields) => {
        if (this.useBeforeCloseEvent) {
            return;
        }
        if(this.typeMode&&this.typeMode===1){
            if(!this.singleSelect){
                this.selectedData = this.selectedData.filter(item=>{
                    if(this.allData.findIndex(element=>{
                        if(item.hasOwnProperty("id")){
                            return element.id===item.id;
                        }else{
                            return element.id===item;
                        }
                    })>-1){
                        return true;
                    }else{
                        return false;
                    }
                });
        
                this.selectedData.forEach((item,index)=>{
                    if(!item.hasOwnProperty("id")){
                        this.allData.forEach(element=>{
                            if(element.id===item){
                                this.selectedData[index] = element;
                            }
                        })
                    }
                });
                helpdata = this.selectedData;
            }
        }

        if (helpdata) {
            // å¤éçè¯éè¦å¤ç,å¸®å©å·²éå·ä¸ºé´é
            if (!this.singleSelect) {
                const idList: string[] = helpdata.map(item => item.id);
                const nameList: string[] = helpdata.map(item => item.name);
                const nameStr = nameList.join();
                const idStr = idList.join();
                this.displayTxt = nameStr;
                this.bindingData.id = idStr;
                this.bindingData.name = nameStr;
            } else {
                this.displayTxt = helpdata.name;
            }
        } else {// ç®åæ¸ç©ºä¼èµ°è¿éï¼èä¸ä¸¤æ¬¡
        }
        // è§¦åäºä»¶
        this.afterConfirm.next(helpdata);
        // this.afterConfirmData.next(this.selctedData);
    }

    /** æ¸ç©ºå */
    afterClear (arg: any) {
        this.clear.next(arg);
    }

    /** æå¨æ¾ç¤ºå¸®å© */
    public showHelp() {
        this.farrisLookUp.showDialog();
    }

    oncheckedChange(arg:any){ 
        if(this.singleSelect){
            this.selectedData=[];
        }       
        if(arg.isCheck){
            arg.data.forEach(item=>{
                if(this.selectedData.indexOf(item.id)===-1){
                    this.selectedData.push(item);
                }
            });
        }else{
            this.selectedData = this.selectedData.filter(item=>{
                if(arg.data.findIndex(element=>{return item.hasOwnProperty("id")?item.id===element.id:item===element.id})>-1){
                    return false;
                }else{
                    return true;
                }
            });
        }
    }

    onOkClick=()=>{
        if (this.useBeforeCloseEvent) {
            return;
        }
        let helpdata :any;
        if(!this.selectedData||this.selectedData.length===0){
            if(this.singleSelect){
                this.afterConfirm.next(helpdata);
            }else{
                this.afterConfirm.next([]);
            }
            this.farrisLookUp.closeDialog();
            return;
        }

        this.selectedData = this.selectedData.filter(item=>{
            if(this.allData.findIndex(element=>{
                if(item.hasOwnProperty("id")){
                    return element.id===item.id;
                }else{
                    return element.id===item;
                }
            })>-1){
                return true;
            }else{
                return false;
            }
        });

        this.selectedData.forEach((item,index)=>{
            if(!item.hasOwnProperty("id")){
                this.allData.forEach(element=>{
                    if(element.id===item){
                        this.selectedData[index] = element;
                    }
                })
            }
        });
        helpdata = this.selectedData;

        if (helpdata&&helpdata.length>0) {
            // å¤éçè¯éè¦å¤ç,å¸®å©å·²éå·ä¸ºé´é
            if (!this.singleSelect) {
                const idList: string[] = helpdata.map(item => item.id);
                const nameList: string[] = helpdata.map(item => item.name);
                const nameStr = nameList.join();
                const idStr = idList.join();
                this.displayTxt = nameStr;
                this.bindingData.id = idStr;
                this.bindingData.name = nameStr;
                this.afterConfirm.next(helpdata);

            } else {
                let nameStr = helpdata[0].name;
                this.displayTxt = nameStr;
                this.afterConfirm.next(helpdata[0]);
            }
        } else {// ç®åæ¸ç©ºä¼èµ°è¿éï¼èä¸ä¸¤æ¬¡
            this.afterConfirm.next(helpdata);
        }
        // è§¦åäºä»¶
        this.farrisLookUp.closeDialog();
    }

}

