import { Injectable } from '@angular/core';
import { Observable, from } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { SysCommonUtilService, SysDataUtil } from '@gsp-sys/sysmgr-common';
import { map } from 'rxjs/operators';
import { GspUserFilter } from '../filter/gsp-user-filter';
import { GspPositionFilter } from '../filter/gsp-position-filter';
import { GspRoleFilter } from '../filter/gsp-role-filter';
import { TreeFilter } from '../filter/tree-filter';

@Injectable()
export class SysHelpService {

    constructor (
        private http: HttpClient,
        private sysUtil: SysCommonUtilService
    ) {
    }

    private getHttpOption (filter: any): { headers: any, params: any } {
        SysDataUtil.deleteEmpty(filter);
        const paramStr = JSON.stringify(filter);
        const opt = {
            headers: this.sysUtil.getHeader()['headers'],
            params: { param: paramStr }
        };
        return opt;
    }

    getUsers (filter: GspUserFilter): Observable<any> {
        const opt = this.getHttpOption(filter);
        // ç¨æ·ç®åçæåé¡µä¿¡æ¯
        return this.http.get(this.sysUtil.userWebApi, opt).pipe(map((val: any) => {
            return val.data;
        }));
    }

    getPositions (filter: GspPositionFilter): Observable<any> {
        const opt = this.getHttpOption(filter);
        return this.http.get(this.sysUtil.positionWebApi, opt).pipe(map((val: any) => {
            return val.data;
        }));
    }
    getRoles (filter: GspRoleFilter): Observable<any> {
        const opt = this.getHttpOption(filter);
        return this.http.get(this.sysUtil.roleWebApi, opt).pipe(map((val: any) => {
            return val.data;
        }));
    }
    getOrgs (filter: TreeFilter): Observable<any> {
        const opt = this.getHttpOption(filter);
        // è§å¾
        opt.params.useView = true;
        return this.handerTreeData(this.http.get(this.sysUtil.sysOrgWebApi + '/all', opt));
    }
    getUserGroups (filter: TreeFilter): Observable<any> {
        const opt = this.getHttpOption(filter);
        return this.handerTreeData(this.http.get(this.sysUtil.userGroupWebApi + '/all', opt));
    }
    getPositionGroups (filter: TreeFilter): Observable<any> {
        const opt = this.getHttpOption(filter);
        return this.handerTreeData(this.http.get(this.sysUtil.posGroupWebApi + '/all', opt));
    }
    getRoleGroups (filter: TreeFilter): Observable<any> {
        const opt = this.getHttpOption(filter);
        return this.handerTreeData(this.http.get(this.sysUtil.roleGroupWebApi + '/all', opt));
    }

    convert2Json(item: any): any {
        const result: any = {
            id: item.id,
            code: item.code,
            name: item.name,
            parentId: item.parentId || '',
            path: item.path,
            layer: item.layer,
            isDetail: item.isDetail,

        };
        if (item.nodeType != null && item.nodeType !== undefined) {
            result.nodeType = item.nodeType;
        }
        return result;
    }
    getParentOrg (org: any): Observable<any> {
        const paramStr = JSON.stringify(this.convert2Json(org));

        const opt = {
            headers: this.sysUtil.getHeader()['headers'],
            params: { param: paramStr }
        };
        return this.http.get(this.sysUtil.sysOrgWebApi+'/getparentorg/0002', opt).pipe(map((val: any) => {
            return val;
        }));
    }

    /** ééKendo-treeViewç»æ */
    private handerTreeData (src: Observable<any>): Observable<any> {
        return src.pipe(map((val: any[]) => {
            from(val).subscribe(item => {
                if (item.layer == 1 || item.parentId == null || item.parentId === '') {
                    item.parentId = undefined;
                }
            });
            return val;
        }));
    }
}
