import { Component, OnInit, ViewChild, Input, EventEmitter, Output, HostListener, ElementRef, AfterContentInit } from '@angular/core';
import { TreeTableComponent, TreeNode } from '@farris/ui-treetable';
import { from } from 'rxjs';
import { GroupService } from './group.service';
import { filter } from 'rxjs/operators';
import { TreeFilter } from '../filter/tree-filter';
import { SysLocaleService } from '../../locale/sys-locale.service';
import { NAVI_LOCALE_ZHCHS } from '../../locale/locales/zh-cn/navigiation';
import { MessagerService } from '@farris/ui-messager';
import { NotifyService } from '@farris/ui-notify';

@Component({
    selector: 'lib-group-nav',
    templateUrl: './group-nav.component.html',
    styleUrls: ['./group-nav.component.css']
})
export class GroupNavComponent implements OnInit, AfterContentInit {

    @ViewChild('farrisTreeParent') farrisTreeParent: ElementRef;
    @ViewChild('farrisTt') farrisTt: TreeTableComponent;
    @Input() title: string;
    @Input() showHeader = true;
    @Input() fixedHeader = true;
    @Input() withCodeColumn = true;
    // æ¯å¦æ·»å æææ ¹èç¹
    @Input() allRoot = false;
    @Input() groupType: 'user' | 'position' | 'role' | 'org';
    // é»è®¤ä¸å¼å¯æ°æ®æéè¿æ»¤
    @Input() filter: TreeFilter;
    @Input() selectKeys: string[] = [];
    @Input() needRemote = true;
    /** æ¾ç¤ºç»ç»ç±»åå */
    @Input() showNodeTypeColumn = false;
    @Input() treeData: TreeNode[];
    @Input() showFilterBar:boolean = false;
    searchFields:any = [{label: "ç¼å·",value: "code"},{label: "åç§°",value: "name"}];
    allColumnsTitle='ææå'
    @Output() search: EventEmitter<any> = new EventEmitter<any>();

    @Output() selectRowChanged: EventEmitter<any> = new EventEmitter<any>();
    // æ¾ç¤ºå
    cols: any[];
    i18nRes: any;

    /** æ¥è¯¢åå®¹ */
    searchInfo = '';
    /** ä¸æ¬¡æ¥è¯¢åå®¹ */
    oldSearchInfo = '';

    field = '';
    /** ä¸æ¬¡æ¥è¯¢åå®¹ */
    oldField = '';

    //ä¸æ¬¡æç´¢ç»æ
    searchResult = [];
    //ä¸æ¬¡æç´¢çä½ç½®
    searchIndex = 0;

    /**
     * çæ§ææ½äºä»¶ï¼éç»ç»ç»TreeTable
     * @param event äºä»¶åæ°
     */
    temp = true;

    @HostListener('window:resize', [])
    winResize() {
        this.reSizeTreeTable();
    }
    constructor(
        public groupService: GroupService,
        public localService: SysLocaleService,
        private notifyService: NotifyService,
        public msg: MessagerService,

    ) {
    }


    /** éç»Farris-TreeTableçå¤§å° */
    public reSizeTreeTable (height: number = 0, width: number = 0) {
        if (height === 0) {
            height = this.farrisTreeParent.nativeElement.clientHeight;
        }
        if (width === 0) {
            width = this.farrisTreeParent.nativeElement.clientWidth;
        }
        this.farrisTt.resize({ height, width });
    }

    ngOnInit () {
        if (this.localService) {
            this.i18nRes = this.localService.getComponentOpt('navi');
        } else {
            this.i18nRes = NAVI_LOCALE_ZHCHS;
        }
        this.searchFields = [{label: this.i18nRes.codeField,value: "code"},{label: this.i18nRes.nameField,value: "name"}];
        this.allColumnsTitle=this.i18nRes.allColumnsTitle;

        // å¯ç¨ç¼å·å
        if (this.withCodeColumn) {
            this.fixedHeader = true;
            this.showHeader = true;
            this.cols = [
                { field: 'code', title: this.i18nRes.codeField, width: 100 },
                { field: 'name', title: this.i18nRes.nameField, width: 100 }
            ];
        } else {
            this.fixedHeader = false;
            this.showHeader = false;
            this.cols = [
                { field: 'name', title: this.i18nRes.nameField, width: 100 }
            ];

        }
        // éè¦è·åæ°æ®ï¼å¦åç´æ¥æ ¹æ®treeDataåå§æ°æ®
        if (this.needRemote) {
            // æææ è¯
            if (this.allRoot) {
                this.treeData = [];
                const rootNode: TreeNode = {};
                rootNode.children = [];
                rootNode.data = {
                    id: 'all',
                    code: this.i18nRes.rootNodeCode,
                    name: this.i18nRes.rootNodeTitle
                };
                rootNode.expanded = true;
                rootNode.leaf = false;
                rootNode.selectable = true;
                this.treeData.push(rootNode);
            } else {
                this.treeData = [];
            }

            this.groupService.getByLayer(1, this.groupType, this.filter).subscribe(val => {
                this.initTreeNodeList(val);
            });
        } else { // ä¸éè¦ä»æå¡å¨ç«¯è·å
            this.farrisTt.updateSerializedValue();
        }

        // å¼æ­¥å è½½ï¼éè¿childrenä¸º0æ¥å¤æ­ä»æªå±å¼è¿èç¹
        this.farrisTt.expand.pipe(filter((node: TreeNode) => node.children.length === 0)).subscribe((node: TreeNode) => {
            node['showLoading'] = true;
            const id = node.data.id;
            // è·åç³»ç»ç»ç»
            this.groupService.getByParentId(id, this.groupType, this.filter).subscribe((list: any[]) => {
                node['showLoading'] = false;
                const childNode: TreeNode[] = [];
                // è½¬æ¢ä¸çº§ç»ç»
                from(list).subscribe(item => {
                    childNode.push(this.convertToTreeNode(item));
                });
                // é²æ­¢åºç°ä¸çº§èç¹æ²¡ææ°æ®
                if (childNode.length > 0) {
                    this.farrisTt.appendChildren(childNode, node);
                } else {// åºè¯¥æ¯æ ä¸çº§æéæèµ°è¿é
                    node.leaf = true;
                    this.farrisTt.updateSerializedValue();
                }
            });
        });
    }

    /**
     * å·æ°å½åé¡µé¢ä¿¡æ¯
     */
    refreshThisNav(){
        this.treeData=[];
        const rootNode: TreeNode = {};
        rootNode.children = [];
        rootNode.data = {
            id: 'all',
            code: this.i18nRes.rootNodeCode,
            name: this.i18nRes.rootNodeTitle
        };
        rootNode.expanded = true;
        rootNode.leaf = false;
        rootNode.selectable = true;
        this.treeData.push(rootNode);
        this.groupService.getByLayer(1, this.groupType, this.filter).subscribe(val => {
            this.initTreeNodeList(val);
        });
    }

    /** é¡µé¢æ¸²æå®æå */
    ngAfterContentInit (): void {
        this.reSizeTreeTable();
    }

    /** æç´¢ï¼ æ­¤å¤æ¯åå±å è½½ï¼æç´¢æ¶éè¦æ ¹æ®åæ°æ®è¿è¡æç´¢ï¼æç´¢åï¼éè¦å°å¯¹åºç¶èç¹å±ç¤º 
     *   @param args  æ ¼å¼ searchData = { field: '*', value: '' };
     */
     searchData(args: any) {
        this.search.next(args);
        if (!this.filter) {
            this.filter = {};
        }
        if(!args){
            return;
        }
        this.searchInfo = args.value;
        this.field = args.field;

        // æç´¢åå®¹ä¸ºç©º
        if (!this.searchInfo || this.searchInfo.length <= 0 ) {
            this.clearSearchValue();
            return;
        }
        
        // æç´¢åå®¹ä¸ä¸æ¬¡ä¸åï¼éæ°æç´¢
        if (this.oldSearchInfo !== this.searchInfo || this.oldField !== this.field) {
            this.filter.isSearchTree = true;
            if(this.field === '*'){
                this.filter.codeOrName = this.searchInfo;
                this.filter.code = '';
                this.filter.name = '';
                    
            }else if(this.field === 'code'){
                this.filter.code = this.searchInfo;
                this.filter.name = '';
                this.filter.codeOrName = '';

            }else if(this.field === 'name'){
                this.filter.name = this.searchInfo;
                this.filter.code = '';
                this.filter.codeOrName = '';

            }

            this.groupService.searchData( this.groupType, this.filter).subscribe(list => {
                this.treeData = [];
                this.searchResult = [];
                this.searchIndex = 0;

                if (list && list.length > 0) {
                    this.treeData = this.makeTree(list);
                    list.forEach(item=>{
                        if(item.search_data){
                            this.searchResult.push(item.id);
                        }
                    })
                    // æå¨æ´æ°TreeTableçå¼
                    this.farrisTt.updateSerializedValue();
                    setTimeout(()=>{
                        this.farrisTt.selectNode(this.searchResult[0]);
                        this.farrisTt.collapseAll();
                        this.expandParent(this.searchResult[0]);
                        this.farrisTt.scrollToCurrentNode();
                    })

                }else{
                    // æå¨æ´æ°TreeTableçå¼
                    this.farrisTt.updateSerializedValue();
                    this.notifyService.info(this.i18nRes.searchNullData);
                }

            });

        }else{
            if(this.searchResult && this.searchResult.length > 0){
                this.searchIndex = this.searchIndex + 1;
                // å·²ç»æç´¢å°æåï¼éè¦éå¤´æç´¢
                if ( this.searchIndex === this.searchResult.length) {
                    this.searchIndex = 0;
                }
    
                setTimeout(()=>{
                    this.farrisTt.selectNode(this.searchResult[this.searchIndex]);
                    this.farrisTt.collapseAll();
                    this.expandParent(this.searchResult[this.searchIndex]);
                    this.farrisTt.scrollToCurrentNode();
                })
            }else{
                this.notifyService.info(this.i18nRes.searchNullData);

            }

        }
        this.oldSearchInfo = this.searchInfo;
        this.oldField = this.field;

    }

    clearSearchValue(){
        //ç¹å»æ¸é¤æ¶ï¼é²æ­¢äºä»¶æ§è¡ä¸¤æ¬¡
        if(!this.temp){
            return;
        }
        this.temp = false;

        // æææ è¯
        if (this.allRoot) {
            this.treeData = [];
            const rootNode: TreeNode = {};
            rootNode.children = [];
            rootNode.data = {
                id: 'all',
                code: this.i18nRes.rootNodeCode,
                name: this.i18nRes.rootNodeTitle
            };
            rootNode.expanded = true;
            rootNode.leaf = false;
            rootNode.selectable = true;
            this.treeData.push(rootNode);
        } else {
            this.treeData = [];
        }
        this.filter.isSearchTree = false;
        this.filter.codeOrName = null;
        this.filter.code = null;
        this.filter.name = null;

        this.groupService.getByLayer(1, this.groupType, this.filter).subscribe(val => {
            this.initTreeNodeList(val);
            if(this.treeData.length>0){
                this.farrisTt.selectNode(this.treeData[0].data.id);

            }
            this.temp = true;
        });
        this.oldSearchInfo = '';
        this.oldField = '';
    }

    /**
     * æé æ 
    */
    private makeTree(data: any[]): TreeNode[] {
        const root = data.filter(t => t.layer === 1).map(t => {
            const node: TreeNode = { 
                data: t ,
                children: [],
                expanded: false,
                selectable: t.farris_selectable};

            if (!t.isDetail) {
                node.leaf = false;
                node.children = [];
            }
            return node;
        });
        root.forEach(e => {
            // æé ä¸çº§ï¼childrenï¼ç¶çº§çidä¿¡æ¯
            e.children = this.makeChildrenByParentId(data, e.data.id);
        });
        return root;
    }

    /**
     * ç¶èç¹æé æ 
     */
    private makeChildrenByParentId(datas: any[], parentId: string): any[] {
        const x = datas.filter(c => c.parentId === parentId).map(d => {
            return {
                data: d,
                children: this.makeChildrenByParentId(datas, d.id),
                expanded: false,
                selectable: d.farris_selectable
            };
        });
        return x;
    }


    /**
     * å°ç»ç»å®ä½è½¬æ¢ä¸ºFarris-TreeNode
     */
    private convertToTreeNode (src: any): TreeNode {
        const node: TreeNode = { data: src };
        // éæç»èç¹
        if (!src.isDetail) {
            node.leaf = false;
            node.children = [];
        }
        // TODO: åªæææéçç»ç»æè½è¢«éä¸­,è¿æ¯è¿éä¸åå¤çï¼åéäºä»¶æ¶å»å¤ç
        node.selectable = src.farris_selectable;
        return node;
    }

    /**
     * åå§é¡¶çº§åç»ï¼é»è®¤layer=1çæåµ
     * @param sysOrgList ç³»ç»ç»ç»åè¡¨
     */
    private initTreeNodeList (list: any[]) {
        if (!list || list.length === 0) {
            return;
        }
        from(list).subscribe(firstLayer => {
            const node = this.convertToTreeNode(firstLayer);
            // éæç»ï¼å¸¦å¾ä¸å¤æé ä¸çº§ï¼åè®¾ææ°æ®
            if (node.leaf) {
                const curId = firstLayer.id;
                const childern = list.filter(item => item.parentId === curId);
                from(childern).subscribe(secondLayer => {
                    node.children.push(this.convertToTreeNode(secondLayer));
                });
            }
            // å¦ææ·»å æææ ¹èç¹
            if (this.allRoot) {
                this.treeData[0].children.push(node);
            } else {
                this.treeData.push(node);
            }
        });
        // æå¨æ´æ°TreeTableçå¼
        this.farrisTt.updateSerializedValue();
    }

    /**
     * éæ©çç»ç»è¡
     * @param arg åæ°
     */
    onNodeSelect (event: any) {
        console.log(event.node.data);
        this.selectRowChanged.next(event.node.data);
    }

    /**
     * è·åå½åTreeTableçç»å®æ°æ®
     */
    public getTreeData (): TreeNode[] {
        return this.treeData;
    }

    /**
     * è·åå½åTreeTableçç»ä»¶å®ä¾
     */
    public getTreeTableComp (): TreeTableComponent {
        return this.farrisTt;
    }
    /**
     * éä¸­æä¸ªèç¹
     */
    public selectNode (id: string, isCheck?: boolean) {
        this.farrisTt.selectNode(id, isCheck);
    }

    /**
     * æ¸é¤ææéä¸­å¼
     */
    public unCheckAllNodes () {
        this.farrisTt.unCheckAllNodes();
    }

    /**
     * æ¸é¤ææéä¸­å¼
     */
    scrollToCurrentNode(key:any){
        if(key){
            this.farrisTt.selectNode(key);
            this.expandParent(key);
            this.farrisTt.scrollToCurrentNode();
        }

    } 
    
    /** éä¸æ¾å°éè¦å±å¼çç¶èç¹ï¼å¨é¨å±å¼ */
    expandParent(key: string) {
        const parent = this.farrisTt.findRowNode(key);
        let parentNode = parent.parent;
        if (parentNode) {
            while (parentNode) {
                this.farrisTt.expandNode(parentNode.id);
                parentNode = parentNode.parent;
            }
        } else {
            // this.farrisTt.expandNode(key);
        }
    }
}

