import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { SysCommonUtilService, SysDataUtil } from '@gsp-sys/sysmgr-common';
import { Observable } from 'rxjs';
import { TreeFilter } from '../filter/tree-filter';

@Injectable({
  providedIn: 'root'
})
export class GroupService {

constructor(
    private http: HttpClient,
    private sysUtil: SysCommonUtilService
) {
}
    private getUrlByGroupType(type: string): string {
        let url = '';
        switch (type) {
            case 'user':
                url = this.sysUtil.userGroupWebApi;
                break;
            case 'position':
                url = this.sysUtil.posGroupWebApi;
                break;
            case 'role':
                url = this.sysUtil.roleGroupWebApi;
                break;
            case 'org':
                url = this.sysUtil.sysOrgWebApi;
                break;
            // é»è®¤ç¨æ·åç»
            default:
                url = this.sysUtil.userGroupWebApi;
                break;
        }
        return url;
     }
    /**
     * æ ¹æ®çº§æ°è·åç»ç»
     * @param layer çº§æ°
     */
    getByLayer(layer: number, type: string, filter: TreeFilter): Observable<any> {
        const url = this.getUrlByGroupType(type);
        if (!filter) {
            filter = {};
        }
        filter.layer = layer;
        filter.parentId = null;
        SysDataUtil.deleteEmpty(filter);
        const opt = {
            headers: this.sysUtil.getHeader().headers,
            params: { param: JSON.stringify(filter) }
        };
        return this.http.get(url, opt);
    }

    /**
     * æ ¹æ®çº§æ°è·åç»ç»
     * @param parentId ç¶èç¹
     */
    getByParentId(parentId: string, type: string, filter: TreeFilter): Observable<any> {
        const url = this.getUrlByGroupType(type);
        if (!filter) {
            filter = {};
        }
        filter.layer = null;
        filter.parentId = parentId;
        SysDataUtil.deleteEmpty(filter);
        const opt = {
            headers: this.sysUtil.getHeader().headers,
            params: { param: JSON.stringify(filter) }
        };
        return this.http.get(url, opt);
    }

    /**
     * æ ¹æ®çº§æ°è·åç»ç»
     * @param parentId ç¶èç¹
     */
    searchData( type: string, filter: TreeFilter): Observable<any> {
        const url = this.getUrlByGroupType(type);
        if (!filter) {
            filter = {};
        }
        filter.layer = null;
        filter.parentId = null;
        SysDataUtil.deleteEmpty(filter);
        const opt = {
            headers: this.sysUtil.getHeader().headers,
            params: { param: JSON.stringify(filter) }
        };
        return this.http.get(url, opt);
    }

}
