import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { BizObjectService } from '../service/biz-object.service';

@Component({
    selector: 'app-biz-object',
    templateUrl: './biz-object.component.html',
    styleUrls: ['./biz-object.component.css']
})
export class BizObjectComponent implements OnInit {

    @Input() bizObjectId: string;
    public data: any[];
    public selectKeys: string[] = [];
    currentid: string;
    /** ä¸å¡å¯¹è±¡ååå */
    @Output()
    bizObjectChanged: EventEmitter<string> = new EventEmitter<string>();
    /** æ æ§ä»¶å±å¼çèç¹å¼ */
    expandedKeys = [];

    constructor (private bizObjectService: BizObjectService) {
    }

    ngOnInit () {
        this.bizObjectService.getBizObjects().subscribe((result: any) => {
            result.forEach(element => {
                if (!element.parentID) {
                    element.parentID = undefined;
                    // å±å¼ç¬¬ä¸çº§
                    if (element.layer === 1) {
                        this.expandedKeys.push(element.id);
                    }
                }
            });
            this.data = result;
            // é»è®¤ä¸å¡å¯¹è±¡çå¤ç
            if (this.bizObjectId && this.bizObjectId !== '') {
                // æ ç¦ç¹è¡
                let curBizObjectId = this.bizObjectId;
                while (curBizObjectId) {
                    this.expandedKeys.push(curBizObjectId);
                    // æ¾å°å½åèç¹çç¶èç¹
                    curBizObjectId = this.data.find(item => item.id === curBizObjectId).parentID;
                }
                // å³ä¾§æä½åå
                this.selectKeys = [this.bizObjectId];
                setTimeout(() => {
                    this.bizObjectChanged.emit(this.bizObjectId);
                }, 80);
            }
        });
    }


    /** éä¸­è¡æ¹ååè§¦åäºä»¶ */
    selectChange (args: any) {
        const curBusId = args.dataItem.id;
        // é¦æ¬¡è§¦å
        if (this.selectKeys == null || this.selectKeys.length === 0) {
            this.bizObjectChanged.emit(curBusId);
        } else {
            // è¿ä¸ªäºä»¶éè¿ä¸ä¼å¯¹selectedkeysæ°ç»èµå¼ï¼æ¯ä¸ä¸ä¸ªç¶æç
            const lastBusId = this.selectKeys[0];
            // ååçæåµä¸è§¦å
            if (lastBusId !== curBusId) {
                this.bizObjectChanged.emit(curBusId);
            }
        }
    }
}

